import React from 'react';
import {
    Row, Col
} from 'antd';
import './index.less';


export default class AttributeItem extends React.Component {
    render() {
        const {name, children} = this.props;
        return (
            <Row className='attribute-item'>
                <Col span="8" className='attribute-item-name'>{name}</Col>
                <Col span="16">
                    {children}
                </Col>
            </Row>
        );
    }
}